body {
  --link-color: #00acdc;
  --header-height: 60px;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
  display: inline-block;
}

.imgblock {
  width: 40px;
  height: 40px;
}

header {
  height: var(--header-height);
  justify-content: space-between;
  align-items: center;
  padding-left: 50px;
  padding-right: 50px;
  font-family: Roboto, sans-serif;
  display: flex;
}

.navListItem {
  align-content: center;
  margin-left: 20px;
  font-style: normal;
}

.navListItem:first-child {
  margin: 0;
}

nav {
  align-content: center;
}

.navItems {
  display: flex;
}

.navLink {
  height: var(--header-height);
  align-content: center;
  font-size: 12px;
  font-weight: 500;
}

.navLink:hover {
  color: var(--link-color);
}

.is-active {
  color: var(--link-color);
  position: relative;
}

.navLink.is-active:after {
  content: "";
  background-color: var(--link-color);
  border-radius: 5px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
